\n// Compute font config\n@include mat-core($fontConfig);\n\n// Theme Config\n\nbody {\n  --primary-color: #bf5700;\n  --primary-lighter-color: #eccdb3;\n  --primary-darker-color: #a93c00;\n  --text-primary-color: #{$light-primary-text};\n  --text-primary-lighter-color: #{$dark-primary-text};\n  --text-primary-darker-color: #{$light-primary-text};\n}\n\n$mat-primary: (\n  main: #bf5700,\n  lighter: #eccdb3,\n  darker: #a93c00,\n  200: #bf5700, // For slide toggle,\n  contrast : (\n    main: $light-primary-text,\n    lighter: $dark-primary-text,\n    darker: $light-primary-text,\n  )\n);\n$theme-primary: mat-palette($mat-primary, main, lighter, darker);\n\nbody {\n  --accent-color: #9cadb7;\n  --accent-lighter-color: #e1e6e9;\n  --accent-darker-color: #80939f;\n  --text-accent-color: #{$dark-primary-text};\n  --text-accent-lighter-color: #{$dark-primary-text};\n  --text-accent-darker-color: #{$dark-primary-text};\n}\n\n$mat-accent: (\n  main: #9cadb7,\n  lighter: #e1e6e9,\n  darker: #80939f,\n  200: #9cadb7, // For slide toggle,\n  contrast : (\n    main: $dark-primary-text,\n    lighter: $dark-primary-text,\n    darker: $dark-primary-text,\n  )\n);\n$theme-accent: mat-palette($mat-accent, main, lighter, darker);\n\nbody {\n  --warn-color: #ff0000;\n  --warn-lighter-color: #ffb3b3;\n  --warn-darker-color: #ff0000;\n  --text-warn-color: #{$light-primary-text};\n  --text-warn-lighter-color: #{$dark-primary-text};\n  --text-warn-darker-color: #{$light-primary-text};\n}\n\n$mat-warn: (\n  main: #ff0000,\n  lighter: #ffb3b3,\n  darker: #ff0000,\n  200: #ff0000, // For slide toggle,\n  contrast : (\n    main: $light-primary-text,\n    lighter: $dark-primary-text,\n    darker: $light-primary-text,\n  )\n);\n$theme-warn: mat-palette($mat-warn, main, lighter, darker);;\n\n$theme: mat-dark-theme($theme-primary, $theme-accent, $theme-warn);\n$altTheme: mat-light-theme($theme-primary, $theme-accent, $theme-warn);\n\n// Theme Init\n@include angular-material-theme($theme);\n\n.theme-alternate {\n  @include angular-material-theme($altTheme);\n}\n\n// Specific component overrides, pieces that are not in line with the general theming\n\n// Handle buttons appropriately, with respect to line-height\n.mat-raised-button, .mat-stroked-button, .mat-flat-button {\n  padding: 0 1.15em;\n  margin: 0 .65em;\n  min-width: 3em;\n  line-height: 36.4px\n}\n\n.mat-standard-chip {\n  padding: .5em .85em;\n  min-height: 2.5em;\n}\n\n.material-icons {\n  font-size: 24px;\n  font-family: 'Material Icons', 'Material Icons';  \n  .mat-badge-content {\n    font-family: 'Roboto';\n  }\n}\n","// Import all the theming functionality.\n// We want overlays to always appear over user content, so set a baseline\n// very high z-index for the overlay container, which is where we create the new\n// stacking context for all overlays.\n$cdk-z-index-overlay-container: 1000 !default;\n$cdk-z-index-overlay: 1000 !default;\n$cdk-z-index-overlay-backdrop: 1000 !default;\n\n// Background color for all of the backdrops\n$cdk-overlay-dark-backdrop-background: rgba(0, 0, 0, 0.32) !default;\n\n// Default backdrop animation is based on the Material Design swift-ease-out.\n$backdrop-animation-duration: 400ms !default;\n$backdrop-animation-timing-function: cubic-bezier(0.25, 0.8, 0.25, 1) !default;\n\n\n@mixin cdk-overlay() {\n  .cdk-overlay-container, .cdk-global-overlay-wrapper {\n    // Disable events from being captured on the overlay container.\n    pointer-events: none;\n\n    // The container should be the size of the viewport.\n    top: 0;\n    left: 0;\n    height: 100%;\n    width: 100%;\n  }\n\n  // The overlay-container is an invisible element which contains all individual overlays.\n  .cdk-overlay-container {\n    position: fixed;\n    z-index: $cdk-z-index-overlay-container;\n\n    &:empty {\n      // Hide the element when it doesn't have any child nodes. This doesn't\n      // include overlays that have been detached, rather than disposed.\n      display: none;\n    }\n  }\n\n  // We use an extra wrapper element in order to use make the overlay itself a flex item.\n  // This makes centering the overlay easy without running into the subpixel rendering\n  // problems tied to using `transform` and without interfering with the other position\n  // strategies.\n  .cdk-global-overlay-wrapper {\n    display: flex;\n    position: absolute;\n    z-index: $cdk-z-index-overlay;\n  }\n\n  // A single overlay pane.\n  .cdk-overlay-pane {\n    // Note: it's important for this one to start off `absolute`,\n    // in order for us to be able to measure it correctly.\n    position: absolute;\n    pointer-events: auto;\n    box-sizing: border-box;\n    z-index: $cdk-z-index-overlay;\n\n    // For connected-position overlays, we set `display: flex` in\n    // order to force `max-width` and `max-height` to take effect.\n    display: flex;\n    max-width: 100%;\n    max-height: 100%;\n  }\n\n  .cdk-overlay-backdrop {\n    // TODO(jelbourn): reuse sidenav fullscreen mixin.\n    position: absolute;\n    top: 0;\n    bottom: 0;\n    left: 0;\n    right: 0;\n\n    z-index: $cdk-z-index-overlay-backdrop;\n    pointer-events: auto;\n    -webkit-tap-highlight-color: transparent;\n    transition: opacity $backdrop-animation-duration $backdrop-animation-timing-function;\n    opacity: 0;\n\n    &.cdk-overlay-backdrop-showing {\n      opacity: 1;\n\n      // In high contrast mode the rgba background will become solid so we need to fall back\n      // to making it opaque using `opacity`. Note that we can't use the `cdk-high-contrast`\n      // mixin, because we can't normalize the import path to the _a11y.scss both for the\n      // source and when this file is distributed. See #10908.\n      @media screen and (-ms-high-contrast: active) {\n        opacity: 0.6;\n      }\n    }\n  }\n\n  .cdk-overlay-dark-backdrop {\n    background: $cdk-overlay-dark-backdrop-background;\n  }\n\n  .cdk-overlay-transparent-backdrop {\n    // Note: as of Firefox 57, having the backdrop be